code {
    padding: 0;
}

p {
    margin: 18px 0 !important;
}

.control-btn {
    margin: 0;
    color: #555;
    padding: 2px 6px;
    background: #fff;
    border: solid 1px #333;
    cursor: pointer;
    -webkit-border-radius: 5px;
    outline: 0 none;
    border-radius: 5px;
    box-shadow: 2px 2px 1px 1px rgba(0, 0, 0, 0.5) !important;
}

.control-on {
    background-color: #2780E3;
    color: #FFF;
}

/* 요약정보레이어 예제 */
.iw_inner {
    padding: 10px;
}

.iw_inner h3 {
    font-size: 16px;
    font-weight: bold;
    padding-bottom: 5px;
    margin: 10px 0;
}

.iw_inner p {
    font-size: 12px;
    color: #333;
}

.iw_inner img.thumb {
    float: right;
    border: solid 1px #aeaeae;
}

#wrap > h2 {
    display: none;
}

#wrap > a.back {
    display: none;
}

#wrap2 > h2 {
    display: none;
}

#wrap2 > a.back {
    display: none;
}

/* HTML Marker */
.cs_mapbridge .map_group .map_marker {
    position: absolute;
    width: 20px;
    height: 20px;
    z-index: 5
}

.cs_mapbridge .map_group .map_marker[class*=_big] {
    z-index: 6
}

.cs_mapbridge .map_group .map_marker .ico {
    position: absolute;
    background: url(/public/img/example/sp_mapbridge5.png) no-repeat;
    background-size: 400px 450px;
    -webkit-background-size: 400px 450px;
    z-index: 10
}

.cs_mapbridge .map_group .map_marker .ico:after {
    position: absolute;
    top: -5px;
    right: -5px;
    bottom: -5px;
    left: -5px;
    background-color: transparent;
    content: '';
    z-index: 30
}

.cs_mapbridge .map_group .map_marker .shd {
    position: absolute;
    top: 0;
    left: 0;
    background: url(/public/img/example/sp_mapbridge5.png) no-repeat;
    background-size: 400px 450px;
    -webkit-background-size: 400px 450px;
    content: ''
}

.cs_mapbridge .map_marker.hp .ico {
    width: 26px;
    height: 36px;
    background-position: -175px 0;
    background-size: 400px 450px;
    -webkit-background-size: 400px 450px;
    transform-origin: 12px 100%;
    -webkit-transform-origin: 12px 100%
}

.cs_mapbridge .map_marker.hp_big .ico {
    width: 38px;
    height: 58px;
    background-position: -175px -45px;
